/* header hover styles */
.mdl-navigation__link {
  position: relative;

  &:after {
    content: '';
    position: absolute;
    width: 100%;
    height: 3px;
    bottom: 0;
    left: 0;
    background-color: $Dolomites-Into-the-Clouds-4-rgba;
    transform: scaleX(0);
    transform-origin: bottom right;
    transition: transform 0.3s;
  }

  &:hover {
    background: none;

    &:after {
      transform-origin: bottom left;
      transform: scaleX(1);
    }
  }
}

